.center{
    text-align: center;
}

.light-theme {
    background-color: #fff;
    color: #444444;
}

nav {
    display: flex;
    text-align: center;
    background-color: #503d81;
    width: 100%;
    padding: 20px;
    color: #f8f8f8;
  }

  .content {
    padding: 0.5em;
    margin: 0 auto;
    max-width: 600px;
    font-size: 1.2rem;
    line-height: 1.1;
    height: 90vh;
  }
  
  .dark-theme {
    background-color: #1f1b24;
    color: white;
  }
  
  .dark-theme nav {
    background-color: #332940;
  }
  
  .dark-theme code {
    color: red;
  }

  .pageheading{
      background-color: blue;
      color:white
  }
